.event-body .imgBox
{
    position: relative;
    min-width: 50%; 
    width: 50%;
    /* min-height: 500px; */
}

img
{
    height: 75%;
    width: 85%;

}

.imgBox .txt {
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 30px 3em;
    width: 83%;
    height: 100%;
  }
.imgBox .txt::before {
    position: absolute;
    top: 30px;
    right: 30px;
    bottom: 30px;
    left: 100%;
    border-left: 4px solid rgba(255, 255, 255, 0.8);
    content: '';
    opacity: 0;
    background-color: rgba(255, 255, 255, 0.5);
    transition: all 0.5s;
    transition-delay: 0.6s;
  }
  .imgBox h2,
  .imgBox p {
    margin: 0 0 5px;
    opacity: 0;
    transition: opacity 0.35s,transform 0.35s;
}
  .imgBox h2 {
    word-spacing: -0.15em;
    font-weight: bolder;
    color: #fff;
    text-transform: uppercase;
    transform: translate3d(30%, 0%, 0);
    transition-delay: 0.3s;

  }

  .imgBox p {
    font-weight: bold;
    color: #00aaff;
    font-size: 1.2em;
    padding-top: 7vh;
    transform: translate3d(0%, 30%, 0);
    transition-delay: 0s;
  }

  .imgBox:hover img {
    opacity: 0.2;
    transition: ease-in-out;
  }
  .imgBox:hover .txt h2 {
    opacity: 1;
    transform: translate3d(0%, 0%, 0);
    transition-delay: 0.4s;
  }
  .imgBox:hover .txt p {
    opacity: 0.9;
    transform: translate3d(0%, 0%, 0);
    transition-delay: 0.6s;
  }
  .imgBox:hover .txt::before {
    background: rgba(255, 255, 255, 0);
    left: 30px;
    opacity: 1;
    transition-delay: 0s;
  }


@media (max-width: 767px){

