
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box
  }
  
  footer {
    box-sizing: border-box;
    position: relative;
    width: 100%;
    height: auto;
    padding: 50px 100px;
    background: #111;
    /* background: #0069cc */
    display: flex;
    justify-content: space-between;
    /* flex-wrap: wrap; */
  }
  footer .container {
    margin: 0;
    display: flex;
    justify-content: space-between;
    /* flex-wrap: wrap; */
    flex-direction: row;
  }
  
  footer .container .sec {
    margin-right: 10px;
  }
  
  footer .container .sec.about__sec {
    width: 32%;
  }
  
  footer .container h2 {
    position: relative;
    color: #fff;
    font-weight: 500;
    margin-bottom: 15px;
  }
  
  footer .container h2::before {
    content: "";
    position: absolute;
    bottom: -5px;
    left: 0;
    width: 50px;
    height: 2px;
    background: #0069cc;
  }
  
  footer p {
    color: #999;
  }
  
  .sci {
    margin-top: 20px;
    display: flex;
  }
  .sci li {
    list-style: none;
  }
  
  .sci li a {
    display: inline-block;
    width: 40px;
    height: 40px;
    background: #222;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
    text-decoration: none;
    border-radius: 4px;
  }
  
  .sci li a:hover {
    background: #0069cc;
  }
  .sci li a .fab {
    color: #fff;
    font-size: 20px;
  }
  
  .quickLinks {
    position: relative;
    width: calc(25%-110px);
  }
  
  .quickLinks li {
    list-style: none;
  }
  
  .quickLinks ul li a {
    
    color: #999;
    text-decoration: none;
    margin-bottom: 10px;
    display: inline-block;
  }
  
  .quickLinks ul li a:hover {
    color: #fff;
  }
  
  .contact {
    width: 23%;
    /* margin-right: 0 !important; */
  }
  
  .contact .info {
    position: relative;
  }
  
  .contact .info li {
    display: flex;
    margin-bottom: 16px;
  }
  
  .contact .info li span:nth-child(1) {
    color: #fff;
    font-size: 20px;
    margin-right: 10px;
  }
  
  .contact .info li span {
    color: #999;
    
  }
  
  .contact .info li a {
    
    color: #999;
    text-decoration: none;
  }
  .copyright {
    width: 100%;
    background: #181818;
    padding: 8px 100px;
    text-align: center;
    color: #999;
  }
  
  img {
    border-radius: 10px;
  }
  
  @media (max-width: 991px) {
    footer {
      padding: 40px;
    }
    footer .container {
      flex-direction: column;
    }
    footer .container .sec {
      margin-right: 0px;
      margin-bottom: 40px;
    }
    footer .container .sec.about__sec,
    .quickLinks,
    .contact {
      width: 100%;
    }
    .copyright {
      padding: 8px 40px;
    }
  }
  